
.guide {
  min-height: 100vh;
  background: #f6f6f6;
  .headerBox {
    display: flex;
    justify-content: center;
    .header {
      width: 100%;
      display: flex;
      justify-content: center;
      .banner {
        width: 100%;
        height: 2.23rem;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }
  .introduceBox {
    display: flex;
    justify-content: center;
    .introduce {
      width: 100%;
      display: flex;
      justify-content: center;
      background: #fff;
      .information {
        width: 92%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 5px 0;
        .information-top {
          width: 100%;
          display: flex;
          margin: 6px 0 0;
          .stream {
            display: flex;
            .stream-theme {
              width: 2.9rem;
              display: flex;
              align-items: center;
              margin: 0px 0 0 5px;
              .title {
                font-size: 16px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #323332;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                line-height: 16px;
              }
            }
          }
        }
        .information-bottom {
          width: 100%;
          display: flex;
          justify-content: flex-end;
          align-items: center;
          margin: 11px 0 6px;
          .info {
            display: flex;
            .browse {
              display: flex;
              img {
                width: 12px;
                height: 12px;
              }
              .number {
                font-size: 13px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #999999;
                line-height: 13px;
                margin: 0 0 0 4px;
              }
            }
          }
        }
      }
    }
  }
  .contentBox {
    display: flex;
    justify-content: center;
    .content {
      width: 90%;
      display: flex;
      flex-direction: column;
      align-items: center;
      .content-top {
        width: 100%;
        display: flex;
        justify-content: center;
        margin: 10px 0 13px;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
        .message {
          font-size: 15px;
          font-family: PingFang SC-中等, PingFang SC;
          font-weight: normal;
          color: #323332;
          line-height: 25px;
        }
      }
      .content-top::-webkit-scrollbar {
        display: none;
      }
      .content-bottom {
        width: 100%;
        display: flex;
        justify-content: center;
        position: fixed;
        bottom: 0;
        .operation {
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          .operation-top {
            width: 100%;
            display: flex;
            justify-content: center;
            margin: 16px 0;
            .subscribe {
              width: 2.85rem;
              height: 0.44rem;
              background: linear-gradient(270deg, #ff5736 0%, #ff2a00 100%);
              border-radius: 22px;
              font-size: 14px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #ffffff;
              line-height: 25px;
            }
          }
        }
      }
      @supports (bottom: constant(safe-area-inset-bottom)) or
        (bottom: env(safe-area-inset-bottom)) {
        .content-bottom {
          padding-bottom: constant(safe-area-inset-bottom);
          padding-bottom: env(safe-area-inset-bottom);
        }
      }
    }
  }
}
